/* Global Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Hero Section */
.products-hero {
  min-height: 424px;
  background-color: #ef6f2e;
  margin-top: 60px;
  margin-bottom: 33px;
  display: flex;
  align-items: flex-end;
  padding-bottom: 40px;
  position: relative;
}

.products-hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(to bottom, rgba(0,0,0,0.3), rgba(0,0,0,0.6));
}

.products-hero .container {
  position: relative;
  z-index: 2;
}

.products-hero h1 {
  font-size: 48px;
  font-weight: 700;
  color: #fff;
  margin: 0;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

/* Products Section */
.products-section {
  margin-bottom: 50px;
  padding: 60px 0;
  background: #2a2522;
}

.products-section .container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

/* Category Filter */
.filter-wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  margin-bottom: 40px;
  padding-bottom: 30px;
  border-bottom: 2px solid #4e4845;
}

.filter-btn {
  padding: 12px 24px;
  background: #393330;
  border: 2px solid #4e4845;
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  border-radius: 4px;
}

.filter-btn:hover {
  background: #4e4845;
  border-color: #ef6f2e;
}

.filter-btn.active {
  background: #ef6f2e;
  border-color: #ef6f2e;
  color: #fff;
}

/* Products Grid */
.products-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
}

/* Product Card */
.product-card {
  display: flex;
  flex-direction: column;
  text-decoration: none;
  background: #26211e;
  border-radius: 12px;
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.product-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
}

/* Product Image */
.product-image {
  width: 100%;
  height: 300px;
  background: #26211e;
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.product-image img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  transition: transform 0.3s ease;
}

.product-card:hover .product-image img {
  transform: scale(1.05);
}

/* Product Info */
.product-info {
  padding: 20px;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
}

.product-name {
  font-size: 16px;
  color: #fff;
  font-family: 'Century Gothic Paneuropean', sans-serif;
  font-weight: 500;
  margin: 0 0 10px 0;
  line-height: 1.4;
}

.product-category {
  font-size: 14px;
  color: #fff;
  font-family: 'Century Gothic Paneuropean', sans-serif;
  opacity: 0.7;
  margin: 0;
}

/* Loading and Error States */
.all-products-container {
  min-height: 100vh;
  background: #2a2522;
  display: flex;
  align-items: center;
  justify-content: center;
}

.loading,
.error {
  text-align: center;
  padding: 40px;
  color: #fff;
  font-size: 18px;
}

/* Responsive */
@media (max-width: 1024px) {
  .products-grid {
    grid-template-columns: repeat(3, 1fr);
  }
  
  .products-hero h1 {
    font-size: 36px;
  }
}

@media (max-width: 768px) {
  .products-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }
  
  .products-hero {
    min-height: 300px;
    padding-bottom: 30px;
  }
  
  .products-hero h1 {
    font-size: 28px;
  }
  
  .products-section {
    padding: 40px 0;
  }
  
  .filter-wrapper {
    gap: 10px;
  }
  
  .filter-btn {
    padding: 10px 18px;
    font-size: 14px;
  }
  
  .product-image {
    height: 250px;
  }
}

@media (max-width: 480px) {
  .products-grid {
    grid-template-columns: 1fr;
  }
  
  .products-hero {
    min-height: 250px;
  }
  
  .products-hero h1 {
    font-size: 24px;
  }
  
  .filter-wrapper {
    gap: 8px;
  }
  
  .filter-btn {
    padding: 8px 16px;
    font-size: 13px;
  }
  
  .product-image {
    height: 200px;
  }
}